.p-header {
  height: rem(85);
  background: transparent;
}

.p-header.change-color {
  background: rgba($color: #111111, $alpha: 1.0);
 }

.p-header__inner{
  padding: 0 rem(20);
  height: inherit;
}

.p-header__logo {
  display: flex;
  align-items: center;
  height: inherit;
}

.p-header__logo img {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.p-header__drawer {
  position: absolute;
   top: rem(10);
   right: rem(10);
   z-index: 5;
}

.p-header__drawer-menu{

}

.p-header__overlay {
  position: fixed;
  z-index: 3;
  width: 100vw;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  height: 100vh;
  background-color: #000;
  color: #fff;
  visibility: hidden;
  opacity: 0;
}

.is-drawerActive {
  .p-header__overlay {
    
    visibility: visible;
    opacity: 0.8;
    transition: 0.3s;
  }
}
 